    .wrap {
        position: relative;
        width: 100%;
        overflow: hidden;
        min-height: 100vh;
        display: flex;
        flex-direction: column;
    }
    
    .Header {
        position: relative;
        background-color: #007bff;
        color: white;
    }

    .Header__container > div {
        height: 3.375em;
    }
    
    .align-center {
        text-align: center;
        align-items: center
    }

    .search {
        margin-top: 50px;
    }

    .margin-class {
        margin: 10px
    }

    .gray-color {
        color: rgba(0,0,0,.56);
        font-size: 13px;
    }
    .small-font {
        font-size: 9px;
    }

    .show {
        display: block;
    }

    .hide {
        display: none;
    }

    .margin-100 {
        margin-left: 91px;
    }

    .bold {
        font-weight: 900;
    }

    .margin-top-10 {
        margin-top: 10px;
    }

    .green {
        color: #34a853;
        font-size: 13px;
    }

    .icon {
        font-size: 22px
    }

    body {
        /* background-image: url("/aircraft.jpg"); */
        background-color: #cccccc;
       }